import { Injectable } from '@angular/core';
import { Http, Headers, Response } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
declare var $: any;
const swal = require('sweetalert');

@Injectable()
export class AuthenticationService {

    public token: string;
    private postHeaders = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded', 'Access-Control-Allow-Origin': 'https://busrep.cyient.com/trusted' });
    constructor(private http: Http) {
        // set token if saved in local storage
        //var currentUser = JSON.parse(localStorage.getItem('currentUser'));
        //this.token = currentUser && currentUser.token;
    }

    getToken(): Observable<boolean> {

        const url = 'http:localhost:8091/TestAPIGateway/trusted';
        const data = new URLSearchParams();
        data.append('username', 'Utilities_NAM');
        data.append('Content-Type', 'application/x-www-form-urlencoded');
        return this.post(url, data) as Observable<any>;
    }

    getTokenForJobs(): Observable<boolean> {
        let postHeaders = new Headers({ 'Content-Type': 'application/json'});

        return this.http.post('https://dte-api.cyient-fiops.com/api/fiops/security/token/cyient', JSON.stringify({ username: 'Raviteja.Viswanadha', password: 'Cyient123' }), { headers: postHeaders })
            .map((response: Response) => {
                // login successful if there's a jwt token in the response
                let token = response.json() && response.json().token;
                if (token) {
                    // set token property
                    this.token = token;

                    // store username and jwt token in local storage to keep user logged in between page refreshes
                    localStorage.setItem('access-token', token);

                    // return true to indicate successful login
                    return true;
                } else {
                    // return false to indicate failed login
                    return false;
                }
            });
    }


    getTableauToken() {
        $.ajax({
            url: 'http:localhost:8091/TestAPIGateway/trusted',
            type: 'POST',
            data: {
                username: 'Utilities_NAM'
            },
            xhrFields: {
                withCredentials: false
            },
            headers: {
                "Content-Type": "application/x-www-form-urlencoded"

            },
            success: function (data) {
                sessionStorage.setItem('tableauTokenId', data);
            },
            error:
            function (data) {
            }

        });

    }

    public post(url: string, postData: any): Observable<any> {
        return this.http
            .post(url, postData, { headers: this.postHeaders })
            .map(this.extractData)
            .catch(this.handleError)
    }

    private handleError(error: any) {
        // In a real world app, we might use a remote logging infrastructure
        // We'd also dig deeper into the error to get a better message

        let errormessage = error._body;
        return Observable.throw(errormessage);
    }

    private extractData(res: Response) {
        let body = res.json();
        if (body) {
            return body.data || body
        } else {
            return {}
        }
    }

}



